import { useContext, useState } from "react";
import "./navbar.scss";
import SearchIcon from "@mui/icons-material/Search";
import { useLocation } from "react-router-dom";
import {
  ChatBubbleOutlineOutlined,
  DarkModeOutlined,
  FullscreenExitOutlined,
  LightMode,
  ListOutlined,
  Menu,
  NotificationsNoneOutlined,
} from "@mui/icons-material";
import MenuIcon from '@mui/icons-material/Menu';
import profilepic from "../../img/profile_user.jpg";
import { Avatar, Button, IconButton, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import AuthContext from "../../context/auth/AuthContext";
import ModeContext from "../../context/mode/ModeContext";
import Dropdown from "../dropdown/Dropdown";

const Navbar = () => {
  //Initializing the use Context to get dispatch method to change dark or light mode
  const { darkMode, dispatch } = useContext(ModeContext);
  // Call Auth Context & Extract isAuthenticated
  const { isAuthenticated, user } = useContext(AuthContext);

  //Initializing the Location Hook
  const location = useLocation();

  const [anchorElUser, setAnchorElUser] = useState(null);

  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };
  return (
    <div className="navbar">
      <div className="wrapper">
        <div className="top">
          <IconButton className="sidebarBtnOpen">
            <MenuIcon />
          </IconButton>
          <Link to="/" style={{ textDecoration: "none" }}>
            <Typography className="brand">Mudasar Fuel</Typography>
          </Link>
        </div>

        {isAuthenticated ? (
          <>
            <div className="items">

              <div className="item">
                <Typography>Hello, {user.name}</Typography>
                {/* <div
                  className="toggle"
                  onClick={() => dispatch({ type: "TOGGLE" })}
                >
                  {darkMode ? (
                    <LightMode className="icon" />
                  ) : (
                    <DarkModeOutlined className="icon" />
                  )}
                </div> */}
              </div>
              <IconButton
                className="item"
                onClick={handleOpenUserMenu}
                sx={{ p: 0 }}
              >
                <Avatar alt="Remy Sharp" src={ user.pic
                ? `http://localhost:5000/public/users/images/${user.pic}`
                : "./img/avatarfile.png"} />
              </IconButton>
            
            <Dropdown
              className="profileMenu"
              anchorElUser={anchorElUser}
              setAnchorElUser={setAnchorElUser}
              BackdropProps={{ invisible: false }}
            />
            </div>
          </>
        ) : (
          <div className="items">
            {location.pathname === "/" ? (
              <div className="item">
                {/* <Link to="/signup" style={{ textDecoration: "none" }}>
                  <Button
                    variant="outlined"
                    style={{ textTransform: "none" }}
                    size="small"
                    className="navlinkBtn"
                  >
                    Signup
                  </Button>
                </Link> */}
              </div>
            ) : (
              <div className="item">
                <Link to="/" style={{ textDecoration: "none" }}>
                  <Button
                    variant="outlined"
                    style={{ textTransform: "none" }}
                    size="small"
                    className="navlinkBtn"
                  >
                    Login
                  </Button>
                </Link>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default Navbar;
